import { ref, onMounted, defineComponent, onBeforeUnmount } from 'vue';
export default defineComponent({
  props: {
    percent: {
      type: Number,
      default: 0
    },
    background: {
      type: String,
      default: '#aaa'
    }
  },

  setup(props) {
    const maskKey = Date.now().toString();
    const wave = ref(null);
    const shadow = ref(null);
    let offset = 0;

    const create_wave = function (percent, swing, offset_) {
      const width = 300;
      const height = 300;
      const distance = 220;
      const waveLength = 500;
      const offset = offset_ % waveLength;
      const circle = (width / waveLength >> 0) + 2;
      let d = 'M ' + (-offset >> 0) + ' ' + ((1 - percent) * height >> 0);
      let d1 = 'M ' + (-offset + distance >> 0) + ' ' + ((1 - percent) * height >> 0); // 每个周期用两个Q

      for (let i = 0; i < circle; i++) {
        d += 'Q ' + (i * waveLength + waveLength / 4 - offset >> 0) + ' ' + ((1 - percent) * height - swing >> 0) + ' ,' + (i * waveLength + waveLength / 2 - offset >> 0) + ' ' + ((1 - percent) * height >> 0);
        d += 'Q ' + (i * waveLength + waveLength * 3 / 4 - offset >> 0) + ' ' + ((1 - percent) * height + swing >> 0) + ',' + (i * waveLength + waveLength - offset >> 0) + ' ' + ((1 - percent) * height >> 0);
        d1 += 'Q ' + (i * waveLength + waveLength / 4 - offset + distance >> 0) + ' ' + ((1 - percent) * height - swing >> 0) + ' ,' + (i * waveLength + waveLength / 2 - offset + distance >> 0) + ' ' + ((1 - percent) * height >> 0);
        d1 += 'Q ' + (i * waveLength + waveLength * 3 / 4 - offset + distance >> 0) + ' ' + ((1 - percent) * height + swing >> 0) + ',' + (i * waveLength + waveLength - offset + distance >> 0) + ' ' + ((1 - percent) * height >> 0);
      }

      d += 'V ' + height + ' H ' + (-offset >> 0) + ' V ' + ((1 - percent) * height >> 0) + ' z';
      d1 += 'V ' + height + ' H ' + (-offset + distance >> 0) + ' V ' + ((1 - percent) * height >> 0) + ' z';
      wave.value && wave.value.setAttribute('d', d);
      shadow.value && shadow.value.setAttribute('d', d1);
    };

    let stopped = false;
    const currentProgress = ref(props.percent);

    const run = function () {
      requestAnimationFrame(() => {
        let perc = props.percent;

        if (props.percent > currentProgress.value) {
          perc = currentProgress.value++;
        } else if (props.percent < currentProgress.value) {
          perc = currentProgress.value--;
        }

        create_wave(perc / 100, 30, offset++ * 10);

        if (!stopped) {
          run();
        }
      });
    };

    onMounted(run);
    onBeforeUnmount(() => {
      stopped = true;
    });
    return {
      maskKey,
      wave,
      shadow
    };
  }

});